<script>
import {axios} from '../plugins/axios.js';
import $ from 'jquery'
export default {
  mounted() {
    $('#about').fadeIn(2000)

    $('.nav-about').click(function () {
      $(window).scrollTop(35)
    })
    $('.nav-skills').click(function () {
      $('#skills').css('display','inline-block')
      $(window).scrollTop(699)

    });
    $('.nav-contact').click(function () {
      $('#Contact').css('display','block');
      $('#skills').css('display','block');

    })
    $('#skills,#Contact,.title-contact,.footer').css({

      'display':'none'
    });
    $(window).scroll(function () {
      if ($(this).scrollTop()>=740) {
        $('#Contact').fadeIn(2000)
      }
      else if ($(this).scrollTop()>=$('#skills').offset().top+34) {
        $('#skills').fadeIn(2000);
        $('.footer').fadeIn(2000)

      }


    })

  },
  data() {
    return {
      name:'',
      subject:'',
      message:'',
      error:false,
      success:false
    }
  },
  computed: {
   nickname() {
     return this.$store.state.nickname;
   }
 },
 methods: {
   submit() {
     if (!this.name||!this.subject||!this.message) {
       this.error=true;
     }
     else if (!this.nickname) {
       this.error=true;
     }
     else {
       this.error=false;
       this.success=true;

       axios.post('https://youssef-project.firebaseio.com/messages.json',{

                  name:this.name,
                  subject:this.subject,
                  message:this.message,

       })
     }
   }
 }
}
</script>

