<template lang="html">
  		<center>
  <div class="signup test mt-4 p-4">

			<h1 class="display-4">Sign up : </h1>
			<form class="">
        <div class="form-group">
          <input type="text" name="" v-model='Nickname' placeholder="nickname" class="form-control" required>

        </div>
        <div class="form-group">
          <input type="text" name="" v-model='spUsername' placeholder="username" class="form-control" required>

        </div>
        <div class="form-group">
          <input type="password" name="" v-model='spPassword' placeholder="password" class="form-control" required>

        </div>
        <div class="form-group">
          <input type="submit" name="" value="submit" @click.prevent='transferData'   class="btn btn-info">

        </div>
        <div class="form-group  ">
          <router-link to='/login' class="lead " >Login ?</router-link>


</div>
			</form>


  </div>
  <p v-if='submitted' class="lead mt-4 submit">Thanks for submitting</p>
  <p class="mt-4 text-danger lead error">Please try again</p>
  		</center>
</template>

<script>
let dataUser = [];

import {axios} from '../plugins/axios.js';
import $ from 'jquery';
export default {
	data() {
		return {
			spUsername:'',
			spPassword:'',
      Nickname:'',
      submitted:false,
		}
	},
  mounted() {
    this.$store.dispatch('act','');

    $('.signup').css('display','none');
    $('div').fadeIn('slow');
  },
	methods: {
		transferData() {

      if (!this.spUsername||!this.spPassword) {
       $('.error').fadeIn();
       this.submitted=false;
        return;
      }
      else {
        $('.error').hide();

        this.submitted=true;
        axios.post('https://youssef-project.firebaseio.com/posts.json',{
          usern:this.spUsername,
          userp:this.spPassword,
          nickname:this.Nickname
        }).then(() => {


        axios.get('https://youssef-project.firebaseio.com/posts.json').then((response) => {

          for (var test in response.data) {
            dataUser.push(response.data[test])
          }

        })



        })


      }





		}
	}
}
</script>

<style lang="css" scoped>

.button {
  background-color: rgb(0, 64, 255);
  border-bottom: 0px;
  color: white;
  padding: 5px 5px;
  font-size: 15px;
  border-radius: 3px;
}
.button:hover {
  background-color: rgb(400, 64, 155);
  border-bottom: 0px;
  color: white;
  padding: 5px 5px;
  font-size: 15px;
  border-radius: 3px;
  cursor: pointer;
  transition: 0.3s;
}
.test {
  background-color: rgb(400, 1000, 422);

  padding: 10px;
  display: inline-block;
  border-radius: 10px;
  box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.10);
  width:auto;
  font-size: 30px;

}
input {
	display: block;
	margin-top: 30px;
		font-size: 15px;




}
.error {
  display: none;
}
</style>
