<template lang="html">
  		<center>
  <div class="login mt-5 bg-light d-inline-block p-4 rounded shadow-lg">

			<section v-if='form'>
				<h1 class="display-4">Login : </h1>
				<form class="">
<div class="form-group mt-4 mb-4 ">

					<input type="text" class="form-control" name="" v-model='lUsername' placeholder="username" required>
        </div>
<div class="form-group mb-2">
  <input type="password" name="" v-model='lPassword' class="form-control" placeholder="password" required>

</div>
          <div class="form-group ">
            <input type="submit" name="" value="submit" @click.prevent='checkingData' class="btn btn-info">

          </div>
          <div class="form-group  ">
            <router-link to='/signup' class="lead " >sign up ?</router-link>


  </div>

				</form>
			</section>


  </div>
  <p  class="error lead mt-4">Please try again</p>

  		</center>
</template>

<script>
import {
	axios
} from '../plugins/axios.js';
import $ from 'jquery';
export default {
	data() {
		return {
			lUsername: '',
			lPassword: '',
			form: true,
			submitted: false,

		}
	},
	mounted() {
    this.$store.dispatch('act','');
    console.log(this.$store.state.nickname);
    console.log(this.$store.state.nickname);
		$('.login').css('display', 'none');
		$('.login').fadeIn('slow');
	},
	methods: {
		checkingData() {
			let dataUser = [];
			axios.get('https://youssef-project.firebaseio.com/posts.json').then(response => {
				for (var name in response.data) {
					dataUser.push(response.data[name]);

				}

				const us = dataUser.find(item => item.usern === this.lUsername);
				const ps = dataUser.find(item => item.userp === this.lPassword);
                  $('.error').fadeIn();

				if (us.usern === this.lUsername && ps.userp === this.lPassword) {

          $('.error').hide();
					this.$router.push({
						path: '/'
					});
					this.$store.dispatch('act',us.nickname);

				}
			})
		},

	},


}
</script>

<style lang="css" scoped>

p {
  font-size: 20px;
  color: red;
  display: none;
}

input {
  margin-top:25px;

}
input {
  margin-bottom: 20px;
  margin-top: 40px;
}
input[type=submit] {
  margin-top: 20px;
}
</style>
